import React from 'react'
import { ListView } from 'antd-mobile';
import {BrowserRouter as  Router, Route, Redirect, NavLink } from 'react-router-dom'

const data = [
  {
    img: 'https://gw.alicdn.com/bao/uploaded/i1/3016544043/O1CN010GvC3o1fjjv0oDEgb_!!3016544043-0-lubanu-s.jpg_500x500q90.jpg_.webp',
    des: '欧式化妆刷收纳筒水晶化妆刷桶金属风笔刷筒眉笔梳子化妆品收纳盒',
    mon: 188
  },
  {
    img: 'https://gw.alicdn.com/bao/uploaded/i3/2962423480/O1CN018beusc1bZssc5XL2Y_!!2962423480.jpg_500x500q90.jpg_.webp',
    des: '创意珐琅彩水杯耐热玻璃杯ins风花茶杯带盖家用泡茶杯子女套装大',
    mon: 228
  },
  {
    img: 'https://gw.alicdn.com/bao/uploaded/i2/2577758093/TB2sRe8fTnI8KJjSszbXXb4KFXa_!!2577758093.jpg_500x500q90.jpg_.webp',
    des: '欧式小清新陶瓷花瓶插花水养鲜花客厅摆件简约现代满天星干花瓶子',
    mon: 348
  },
  {
    img: 'https://gw.alicdn.com/bao/uploaded/i4/550777627/O1CN01qZBKLR26DDOzVnIEp_!!0-item_pic.jpg_500x500q90.jpg_.webp',
    des: '卡通小熊定时器机械发条计时器 厨房做饭温奶提醒器宝宝提示闹钟',
    mon: 99
  },
  {
    img: 'https://gw.alicdn.com/bao/uploaded/i1/40016731/O1CN0142hCrt1zaqPI6BNWs_!!40016731.jpg_500x500q90.jpg_.webp',
    des: '日本Amabro有田烧描金手绘陶瓷小碟子调味碟蘸料 日式豆皿酱油碟',
    mon: 128
  },
  {
    img: 'https://gw.alicdn.com/bao/uploaded/i4/2437156679/TB1pLCSs2uSBuNkHFqDXXXfhVXa_!!0-item_pic.jpg_500x500q90.jpg_.webp',
    des: '北欧简约地毯客厅卧室床边毯ins网红地毯长方形家用可水洗定制',
    mon: 690
  },
];
const NUM_ROWS = 10;
let pageIndex = 0;

function genData(pIndex = 0) {
  const dataBlob = {};
  for (let i = 0; i < NUM_ROWS; i++) {
    const ii = (pIndex * NUM_ROWS) + i;
    dataBlob[`${ii}`] = `row - ${ii}`;
  }
  return dataBlob;
}

class Things extends React.Component {
  constructor(props) {
    super(props);
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2,
    });

    this.state = {
      dataSource,
      isLoading: true,
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.rData = genData();
      this.setState({
        dataSource: this.state.dataSource.cloneWithRows(this.rData),
        isLoading: false,
      });
    }, 600);
  }

  onEndReached = (event) => {
    if (this.state.isLoading && !this.state.hasMore) {
      return;
    }
    this.setState({ isLoading: true });
    setTimeout(() => {
      this.rData = { ...this.rData, ...genData(++pageIndex) };
      this.setState({
        dataSource: this.state.dataSource.cloneWithRows(this.rData),
        isLoading: false,
      });
    }, 1000);
  }

  render() {
    const separator = (sectionID, rowID) => (
      <div
        key={`${sectionID}-${rowID}`}
        style={{
          backgroundColor: '#ECECEC',
          height: 8,
          borderTop: '1px solid #ECECEC',
          borderBottom: '1px solid #ECECEC',
        }}>
      </div>
    );
    let index = data.length - 1;
    const row = (rowID) => {
      if (index < 0) {
        index = data.length - 1;
      }
      const obj = data[index--];
      return (
        <div 
          key={rowID} 
          style={{backgroundColor:'#ECECEC'}}
        >
          <NavLink to='/shop1'>
            <div style={{ 
              display: "inline-block", 
              marginLeft: '10px', 
              borderRadius:'8px',
              backgroundColor:'white' 
            }}>
              <img 
                style={{width: '170px', height: '170px'}} 
                src={obj.img} 
                alt="" />
              <div style={{
                width: '170px', 
                position:'relative', 
                lineHeight: 1.5
              }}>
                <div style={{
                  height:'32px', 
                  marginBottom: '8px', 
                  color:'black', 
                  fontSize:'12px', 
                  overflow:"hidden"
                }}>
                  {obj.des}
                </div>
                <div>
                  <span style={{ fontSize: '15px', color: '#FF6E27' }}>
                    {'￥ '+ obj.mon}
                  </span>
                </div>
              </div>
            </div>
          </NavLink>
          <NavLink to='/shop2'>
            <div style={{ 
              display: "block", 
              float:'right',
              marginRight: '10px', 
              borderRadius:'5px',
              backgroundColor:'white'
            }}>
              <img 
                style={{width: '170px', height: '170px' }} 
                src={obj.img} 
                alt="" />
              <div style={{width: '170px', position:'relative', lineHeight: 1.5}}>
                <div style={{
                  height:'32px', 
                  marginBottom: '8px', 
                  color:'black', 
                  fontSize:'12px', 
                  overflow:"hidden"
                }}>
                  {obj.des}
                </div>
                <div>
                  <span style={{ fontSize: '15px', color: '#FF6E27' }}>
                    {'￥ '+ obj.mon}
                  </span>
                </div>
              </div>
            </div>
          </NavLink>
        </div>
      );
    };
    return (
      <ListView
        ref={el => this.lv = el}
        dataSource={this.state.dataSource}
        renderHeader={() => 
          <span style={{
            width:'100px',
            color: 'orangered', 
            textAlign: 'center', 
            padding:'100px'
          }}>
            →❤你可能还喜欢←
          </span>}
        renderFooter={() => (
          <div style={{ padding: 50, textAlign: 'center' }}>
            {this.state.isLoading ? 'Loading...' : 'Loaded'}
          </div>
        )}
        renderRow={row}
        renderSeparator={separator}
        className="am-list"
        useBodyScroll
        scrollRenderAheadDistance={500}
        onEndReached={this.onEndReached}
        onEndReachedThreshold={5}
        quickSearchBarTop
        horizontal={true}
      />
    );
  }
}

export default Things