import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class BuyTab extends React.Component {
  render() {
    const { pathname } = this.props.location;
    const numarr = pathname.split('');
    const numa = numarr.slice(numarr.length-1,numarr.length);
    const num = numa.join('');
    // console.log(num)
    return (
      <div 
        style={{
          position: 'fixed',
          width: '100%',
          bottom: 0,
          backgroundColor:'white'
        }}>
        <div 
          style={{
            position: 'relative',
            width: '30%',
            bottom: 0,
            float:'left',
            marginRight:'20px'
          }}>
          <TabBar
            unselectedTintColor="#949494"
            tintColor="rgb(255,133,42)"
            barTintColor="white">
            <TabBar.Item
              title="店铺"
              key="shop"
              icon={
                <i className='iconfont icon-dianpu'></i>                           
              }
              selectedIcon={
                <i className='iconfont icon-dianpu'></i>                           
              }
              selected={ pathname === `/shop${num}` }
              onPress={() => {
                this.props.history.push(`/shop${num}`);
              }}
              data-seed="logId" >
            </TabBar.Item>
            <TabBar.Item
              icon={
                <i className='iconfont icon-kefu'></i>                           
              }
              selectedIcon={
                <i className='iconfont icon-kefu1'></i>                           
              }
              // selected={ pathname === '/' }
              // onPress={ () => {
              //     this.props.history.push('/');
              // } }
              title="客服"
              key="hear"
              data-seed="logId1">
            </TabBar.Item>
            <TabBar.Item
              icon={
                <i className='iconfont icon-shoucang'></i>
              }
              selectedIcon={
                <i className='iconfont icon-shoucang1'></i>
              }
              // selected={ pathname === '/' }
              // onPress={ () => {
              //     this.props.history.push('/');
              // } }
              title="收藏"
              key="shoucang">
            </TabBar.Item>
          </TabBar>
        </div>
        <span 
          style={{
            display:'flex',
            float:'left',
            width:'60%',
            border:'1px solid transparent',
            marginTop:'5px'}}>
          <div 
            style={{
              height:'35px',
              width:'50%',
              borderRadius:'20px 0 0 20px',
              background:'-webkit-linear-gradient(left, #FFC500,#FF9402)',
              textAlign:'center',
              lineHeight:'35px',
              color:'white'}}>
                加入购物车
            </div>
          <div 
            style={{
              height:'35px',
              width:'50%',
              borderRadius:'0 20px 20px 0',
              background:'-webkit-linear-gradient(left, #FF7A00,#FE560A)',
              textAlign:'center',
              lineHeight:'35px',
              color:'white'}}>
                立即购买
          </div>
        </span>
      </div>
    );
  }
}

export default withRouter(BuyTab);